import React, { lazy, Suspense } from "react";

const MdOutlineRoom = lazy(() => import("react-icons/md").then(module => ({ default: module.MdOutlineRoom })));
const MdOutlineBedroomParent = lazy(() => import("react-icons/md").then(module => ({ default: module.MdOutlineBedroomParent })));
const BsPersonCircle = lazy(() => import("react-icons/bs").then(module => ({ default: module.BsPersonCircle })));
const FaShower = lazy(() => import("react-icons/fa").then(module => ({ default: module.FaShower })));
const BiArea = lazy(() => import("react-icons/bi").then(module => ({ default: module.BiArea })));

const HomeCard = React.memo(({ id, title, zipCode, city, level, rooms, photos, price, livingArea, salerEmail, publicationId }) => {
  return (
    <div className="card">
      <img src={photos.length > 0 && photos[0]} alt="view on your future home" />

      <div className="info">
        <h3> {`${title} - ${rooms} pièce(s)`} </h3>
        <p className="info-icon-text">
          <Suspense fallback={<span>...</span>}>
            <MdOutlineRoom className="info-icon" />
          </Suspense>
          {`${zipCode}  ${city}`}
        </p>
        <p className="info-icon-text"> REF <span>{publicationId}</span> </p>
        <h5 className="info-icon-text">
          <Suspense fallback={<span>...</span>}>
            <BsPersonCircle className="info-icon" />
          </Suspense>
          {/* <span className="proprio">John Doe</span> */}
          <span>{price} £</span>
        </h5>
        <p> Ce logement situé au niveau {level}, est composé de {rooms} chambre(s). </p>
        <p className="info-icon-text">
          <Suspense fallback={<span>...</span>}>
            <BiArea className="info-icon" />
          </Suspense>
          {livingArea} m²
          <Suspense fallback={<span>...</span>}>
            <FaShower className="info-icon" />
          </Suspense>
          <Suspense fallback={<span>...</span>}>
            <MdOutlineBedroomParent className="info-icon" />
          </Suspense>
          {rooms} p
        </p>
      </div>
    </div>
  );
});

export default HomeCard;
